import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument, DocumentReference } from '@angular/fire/firestore';
import { map, take } from 'rxjs/operators';
import { Observable } from 'rxjs';
 
 
@Injectable({
  providedIn: 'root'
})
export class ProductsService {
  private markets;
  private marketCollection: AngularFirestoreCollection;
 
  constructor(private afs: AngularFirestore) {
    this.marketCollection = this.afs.collection('market');
    this.markets = this.marketCollection.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
  }
 
  getMarkets() {
    return this.markets;
  }
 
  getMarket(id) {
    return this.marketCollection.doc(id).valueChanges();
  }
 
  addMarket(market) {
    return this.marketCollection.add(market);
  }
 
  updateMarket(market){
    return this.marketCollection.doc(market.id).update(market);
  }
 
  deleteMarket(id){
    return this.marketCollection.doc(id).delete();
  }
}